.reference {
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0.7;
    border-radius: 10px;
    scroll-margin-top: 80px;
    outline: none;
}

.reference:hover {
    transition: all 0.5s ease;
    background-color: rgba(0, 0, 0, 0.1);
}